#app {
  display: block;
  height: 100vh;
}

#map {
  width: 100%;
  height: 95vh;
}

/* Re-anchor legend since sidebar is gone */
.legend {
  position: absolute;
  bottom: 12px;
  left: 12px;
}

.map .base__hero {
  border: 1px solid #f1f1f1;
  border-radius: 6px;
  position: absolute;
  width: 33vw;
  z-index: 9;
}
.map .base__hero .blurb {
  padding: 30px;
}
.map .btn.active {
  background: #fff;
  border-radius: 30px;
  border: 2px solid #fff;
  box-shadow: none;
  color: #000;
  cursor: pointer;
  display: inline-block;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.5em;
  margin-bottom: 20px;
  outline: none !important;
  padding: 5px 30px;
  position: relative;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  text-align: center;
  text-decoration: none !important;
  user-select: none;
  vertical-align: middle;
  white-space: normal;
  z-index: 1;
  -webkit-transition: background 0.3s linear, border 0.3s linear, color 0.3s linear;
  -moz-transition: background 0.3s linear, border 0.3s linear, color 0.3s linear;
  -ms-transition: background 0.3s linear, border 0.3s linear, color 0.3s linear;
  -o-transition: background 0.3s linear, border 0.3s linear, color 0.3s linear;
  transition: background 0.3s linear, border 0.3s linear, color 0.3s linear;
}
.map .btn.active:hover, .map .btn.active:focus {
  background: #737373;
  border: 2px solid #737373;
  color: #fff;
}